import logo from "./logo.svg";
import "./App.css";
import { Form, Input, Button, message } from "antd";
import { useEffect } from "react";
import axios from "axios";

function App() {
  useEffect(() => {
    var screenW = document.documentElement.clientWidth;
    var screenH = 180;
    for (var i = 0; i < 40; i++) {
      var span = document.createElement("span");
      document.getElementsByClassName("starHeader")?.[0].appendChild(span);
      var x = parseInt(Math.random() * screenW);
      var y = parseInt(Math.random() * screenH);
      span.style.left = x + "px";
      span.style.top = y + "px";
      span.style.zIndex = "0";
      var scale = Math.random() * 1.5;
      span.style.transform = "scale(" + scale + ", " + scale + ")";
      var rate = Math.random() * 30;
      span.style.animationDelay = rate + "s";
      span.style.animationDuration = 6 + "s";
    }
  }, []);

  const getSheetsId = async () => {
    const id = (
      await axios({
        method: "post",
        url: "https://open.feishu.cn/open-apis/sheets/v3/spreadsheets/D3jIskJNYhiVNotcFuDcCCO4nOg/sheets/query",
        headers: {
          Authorization: "u-cLy83CquJ6HF_zN4H_C3JBhh3UlkkhLxrq00g5A8yB.J",
          "Content-Type": "application/json",
        },
      })
    )?.data?.sheets?.[0]?.sheet_id;
    return id;
  };

  const writeData = (values) => {
    axios({
      method: "post",
      url: "https://2557a42ca80d4786ada1dfce6da28d3b.apig.cn-east-3.huaweicloudapis.com/writeData",
      data: {
        name: values.name,
        number: values.number,
        time: Date.now(),
      },
    }).then((res) => {
      if (JSON.parse(res?.data?.success)) {
        message.info("提交成功");
      }
    });
  };
  return (
    <div className="App">
      <div className="starHeader"></div>
      <div className="title">云店周新增激活商户数预测收集表</div>
      <Form
        onFinish={(values) => {
          console.log(4444, values);
          if(true){
            message.error('已过截止时间，无法提交')
            return;
          }
          writeData(values);
        }}
      >
        <Form.Item label={"姓名："} name="name">
          <Input placeholder="请输入"></Input>
        </Form.Item>
        <Form.Item label={"预测周新增激活商户数："} name="number">
          <Input placeholder="请输入" type="number"></Input>
        </Form.Item>
        <div className="btnWrap">
          <Button type="primary" htmlType="submit" className="btn">
            提交
          </Button>
        </div>
      </Form>
    </div>
  );
}

export default App;
